.switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 16px;
    margin: 0 0 0 8px
  }
  
.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #282c34;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  left: 4px;
  bottom: 4px;
  background-color: #e96913;
  transition: .4s;
}

input:checked + .slider {
  background-color: #e5e5e5;
}

input:focus + .slider {
  box-shadow: 0 0 1px #e5e5e5;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
